	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Grinding Away","date":"10 Dec 2002","slug":"/2002-12-10-grinding-away/","tags":"Y2002"},"html":"<p>Work has been a grind, as closing up an office 500 km away is a lot more work than just turning out the lights. Projects have to be transferred, equipment accounted for and transported, security schemes dismantled, accounts cancelled. Not to mentioned that it's emotionally draining. The colleagues in Leipzig are being paid until the end of the month, but as of tomorrow their office will be dismantled so they have no place, nor reason, to work.</p>\n<p>Meanwhile, in Hamburg, the <em><acronym title=\"Work Council\">Betriebsrat</acronym></em> held a meeting with us that explained their side of recent events. It looks like next year will continue to be 'interesting'. Management tried to organize a Christmas party at the last minute, but cancelled when it was clear that there was little interest. Meanwhile, we have coffee again. It turns out providing coffee outright is cheaper than renting vending machines that dispense coffee.</p>\n<p>(Question for markup gurus: is defining foreign words, as in <em><acronym title=\"Work Council\">Betriebsrat</acronym></em>, an acceptable use of the <code>&lt;acronym&gt;</code> tag?)</p>\n<p><b>Update</b> Tim says I should do this: <code>&lt;span xml:lang=\"de\" title=\"kindergarten\"&gt;Kindergarten&lt;span&gt;</code>. Let's see how it looks: <span xml:lang=\"de\" title=\"kindergarten\" class=\"foobar\">Kindergarten</span>. Hmmm, the tool tip is there, but I'd need to define a style. He's right, that's way too hardcore! ;-)</p>\n<p><b>Further update</b> The default style in Mozilla for <code>&lt;acronym&gt;</code> is <code>{ border-bottom: dotted 1px; }</code>. So let's see how <code>&lt;span xml:lang=\"de\" title=\"kindergarten\" style=\"border-bottom: dotted 1px;  font-style: italic;\"&gt;Kindergarten&lt;span&gt;</code> looks: <span xml:lang=\"de\" title=\"kindergarten\" style=\"border-bottom: dotted 1px;  font-style: italic;\">Kindergarten</span>. Hmmm, that's not too bad.</p>"},"next":{"fields":{"title":"Advent Calendar at leo.org","date":"09 Dec 2002","slug":"/2002-12-09-advent-calendar-at-leoorg/"}},"previous":{"fields":{"title":"Packages","date":"10 Dec 2002","slug":"/2002-12-10-packages/"}}},{"node":{"fields":{"title":"Advent Calendar at leo.org","date":"09 Dec 2002","slug":"/2002-12-09-advent-calendar-at-leoorg/","tags":"Y2002"},"html":"<p>There's a cute <a href=\"http://www.leo.org/wkal/\">Advent calendar</a> at the <a href=\"http://dict.leo.org\">leo.org</a> German-English dictionary. No sound, no flash, and the surprises behind the dates aren't all that great. I just like the picture of the 'teddy bear bakery'.</p>"},"next":{"fields":{"title":"Cold Legs","date":"08 Dec 2002","slug":"/2002-12-08-cold-legs/"}},"previous":{"fields":{"title":"Grinding Away","date":"10 Dec 2002","slug":"/2002-12-10-grinding-away/"}}},{"node":{"fields":{"title":"Cold Legs","date":"08 Dec 2002","slug":"/2002-12-08-cold-legs/","tags":"Y2002"},"html":"<p>Mama's under the weather, so Christopher and I hiked up to Hamburg for the afternoon to visit the Christmas markets downtown. I decided that we would take the train rather than drive, and thus give Christopher his first ride in a real live Deutsche Bahn train (as opposed to a mere S- or U-Bahn). I had forgotten that it was not only cold but windy (so it was miserable outside at the market), and that there was a soccer match so that the main station was full of HSV fans (so we missed the train I wanted to take home and had to wait an hour for the next one), and that the return train would be packed to the brim with returning weekend travelers (so we had to stand... ever try to get a 3-year-old to stand still for 25 minutes?). In return, the DB never checked our tickets, which I was planning to buy on board, so the trip was relatively inexpensive.</p>\n<p>When we got home, Christopher told Mama all about trains for a good half hour. So I guess he enjoyed the trip.</p>"},"next":{"fields":{"title":"Party Like It's 1968","date":"07 Dec 2002","slug":"/2002-12-07-party-like-its-1968/"}},"previous":{"fields":{"title":"Advent Calendar at leo.org","date":"09 Dec 2002","slug":"/2002-12-09-advent-calendar-at-leoorg/"}}}],"pathPrefix":"","first":false,"last":false,"index":808,"pageCount":1101}}

